import React from "react";
import "./TopButton.css";

export default function TopButton({ theme }) {
  function GoUpEvent() {
    document.body.scrollTop = 0;
    document.documentElement.scrollTop = 0;
  }

  function scrollFunction() {
    var div = document.getElementById("topButton");
    if (
      (document.body.scrollTop > 40 ||
      document.documentElement.scrollTop > 40) && div
    ) {
      div.style.visibility = "visible";
    } else {
      if (div) div.style.visibility = "hidden";
    }
  }

  window.onscroll = function () {
    scrollFunction();
  };


  return (
    <div
      onClick={GoUpEvent}
      id="topButton"
      style={{
        color: theme.body,
        backgroundColor: theme.mouseBgColor,
      }}
      title="Go up"
    >
      <span className="iconify social-media-icons" aria-hidden="true" id="arrow" data-icon="ps:up" style={{ color: theme.color, backgroundColor: "transparent" }}></span>
    </div>
  );
}
